import http from "axios"
import store from "@/store/index.js" //vuex
import router from "@/router/index.js" //路由
import NProgress from "nprogress" //引入进度条插件
import "nprogress/nprogress.css" //引入进度条样式

let httpRequest
if(process.env.NODE_ENV === "development"){//开发模式
	httpRequest = http.create({
		baseURL:process.env.VUE_APP_BASE_URL,
	})
}else if(process.env.NODE_ENV === "production"){//上线模式
	httpRequest = http.create({
		baseURL:"/api"	
	})
}


//请求拦截
httpRequest.interceptors.request.use(function(config) {
	NProgress.start() //加载中
	if (store.state.userModel.token) {
		config.headers.token = store.state.userModel.token
	}
	return config;
}, function(error) {
	return Promise.reject(error);
});

//响应拦截
httpRequest.interceptors.response.use(function(config) {
	if (config.data.code === "1006") {
		store.commit("userModel/reflashToken", config.data.token)
	} else if (config.data.code === "1007") {
		store.commit("userModel/removeToken")
		router.replace("/login")
	}
	NProgress.done() //加载完毕
	return config;
}, function(error) {
	return Promise.reject(error);
});

//封装GET
function ajaxGet(url, config) {
	return new Promise(function(resolve) { //get请求
		httpRequest.get(url, config).then(function(res) {
			resolve(res)
		})
	})
}
//封装POST
function ajaxPost(url, data, config) {
	return new Promise((resolve) => {
		httpRequest.post(url, data, config).then(function(res) {
			resolve(res)
		})
	})
}

export {
	ajaxGet,
	ajaxPost
}
